import React ,{useState}from 'react'


export default function TextForm(props) {
    
    const handleOnUpperClick = () =>{
        console.log('upper case btn clicked '+text)
        let newText=text.toUpperCase();
        setText(newText)
        props.showAlert("upper case operation done!!!","success")

    }
    const handleOnLowerClick = () =>{
      console.log('lower case btn clicked '+text)
      let newText=text.toLowerCase();
      setText(newText)
      props.showAlert("lower case operation done!!!","success")


  }
  const handleOnClearClick = () =>{
    console.log('clear case btn clicked '+text)
    let newText="";
    setText(newText)
    props.showAlert("clear operation done!!!","success")

}
    const handleOnChange = (event)=>{
     console.log("on change")
     setText(event.target.value)
    }
     
    const handleOnCopy=()=>{
      var text = document.getElementById("exampleFormControlTextarea1");
      text.select();
      navigator.clipboard.writeText(text.value);
      props.showAlert("all text is copied at clipboard !!!","success")

    }

    const handleOnExtraSpaces=()=>{
      let newText=text.split(/[ ]+/);
      setText(newText.join(" "))
      props.showAlert("all extra spaces are removed!!!","success")

    }

    const [text,setText]=useState('')


  return (
    <div className='container'  style={{backgroundColor:props.mode === 'dark'?'#6f5fc9':'white', color: props.mode==='light'?'black':'white'}}>
        <h3>{props.heading}</h3>
<div className="mb-3 ">
    
  <textarea className="form-control" style={{backgroundColor:props.mode === 'dark'?'gray':'white' , color: props.mode==='light'?'black':'white'}} id="exampleFormControlTextarea1" value={text} onChange={handleOnChange} rows="8"></textarea>
  <button className='btn btn-primary mx-2 my-3' onClick={handleOnUpperClick}>Convert to upper case</button>
  <button className='btn btn-primary' onClick={handleOnLowerClick}>Convert to lower case</button>
  <button className='btn btn-primary mx-2 my-3' onClick={handleOnClearClick}>Clear Text</button>
  <button className='btn btn-primary mx-2 my-3' onClick={handleOnCopy}>Copy Text</button>
  <button className='btn btn-primary mx-2 my-3' onClick={handleOnExtraSpaces}>remove Extra spaces</button>

</div>
<h2>summary of your text</h2>
<p>{text.split(" ").length}  Words {text.length} Charecters</p>
<p>{0.008 * text.split(" ").length} minutes required for reading</p>
<h1>Preview</h1>
<p>{text.length>0?text:'enter some text on the above text area'}</p>
    </div>
  )
}
