import logo from './logo.svg';
import './App.css';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import About from './components/About';
import Alert from './components/Alert';
import { useState } from 'react';
import { type } from '@testing-library/user-event/dist/type';

// import{
//   BrowserRouter as Router,
//   Routes,
//   Route,
//   Link
// } from "react-router-dom";




function App() {
  const [alert,setAlert]=useState(null)
  const showAlert=(message,type)=>{
    setAlert({
      msg : message,
      type : type
    })
    setTimeout(()=>{
      setAlert(null);
    },3000)   
  }


  const [mode,setMode]=useState('light');
  const [disableEnable,setDisableEnable]=useState("Enable")

  const toggleMode =()=>{
    if(mode === 'light'){
      setMode("dark")
      document.body.style.backgroundColor="#6f5fc9"
      showAlert("dark mode activited!!!","success")
      document.title="Textutile dark mode Enable"
      setTimeout(()=>{
        document.title="Textutile"

      },3000)   
    
      // setInterval(() => {
      //   document.title="Textutile is amazing";
      
      // }, 2000);
      // setInterval(() => {
      //   document.title="Install Textutile Now";
      
      // }, 1500);
    }
    else{
      setMode("light")
      document.body.style.backgroundColor="white"
      showAlert("light mode activited!!!","success")
      document.title="Textutile light mode Enable"
      setTimeout(()=>{
        document.title="Textutile"

      },3000)   
    
 

    }

    if(disableEnable==="Disable"){
      setDisableEnable("Enable");
    }
    else{
      setDisableEnable("Disable")
    }
  }

  


  return (
  <>

  {/* <Router> */}

<Navbar Title="MQM TextUtils" Home="Home" mode={mode} toggleMode={toggleMode} disableEnable={disableEnable}/>
<Alert alert={alert} />
{/* <About/> */}
<TextForm heading="Enter text to analyze" mode={mode} showAlert={showAlert}/>
{/* <Routes>
    <Route path="/about" element={<About/>}/>
      
    <Route path="/" element={<TextForm heading="Enter text to analyze" mode={mode} showAlert={showAlert}/>}/>
    
</Routes>
 </Router> */}
  
   </>
  );
}

export default App;
